import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Grid, Typography, withStyles } from '@material-ui/core'
import FileDrop from 'react-file-drop';
import DropImage from '../assets/files.png'

export class DropArea extends Component {

  render() {
    const { classes } = this.props
    return (
      <Grid container alignItems='center' justify='center'>
        <Grid item>
          <FileDrop onDrop={this.handleDrop}>
            <Grid container className={classes.dropHolder}>
              <Grid item container justify='center'>
                <img src={DropImage} alt={'fromFile'} width='50%' height='40%' /></Grid>
              <Grid item>
                <Typography className={classes.dropText}> Drag and Drop some files here!</Typography>
              </Grid>
            </Grid>
          </FileDrop>
        </Grid>
        
      </Grid>
    )
  }
}

const mapStateToProps = (state) => ({

})

const mapDispatchToProps = {

}
const styles = theme => ({
  dropHolder: {
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center'
  },
  dropText:{
    fontSize:20,
    fontWeight:'bold',
    color:'gray'
  }
})
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DropArea))
